html, body, #root {
    height: 100%;
    margin: 0;
}

.App {
    font-family: Roboto, sans-serif;
    height: 100%;
    display: flex;
    width: 100%;
    padding-top: 25px;
    padding-bottom: 25px;
    justify-content: center;
    overflow-x: auto;
}

.Flag {
    border-radius: 100px;
    /*background: red;*/
    overflow: hidden;
    height: 32px;
    width: 32px;
}

.main-wrapper {
    display: flex;
    flex: 1;
}

.Section {
    width: 100%;
    /*height: 100%;*/
    align-items: center;
    padding-top: 10px;
    padding-bottom: 10px;
    border-radius: 12px;
}

.left-block {
    width: 30%;
    height: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}

.right-block {
    width: 70%;
    height: 100%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}
